import {Component, OnInit} from '@angular/core';
import {FormBuilder, Validators} from '@angular/forms';
import {NzMessageBaseService, NzMessageService, NzModalService} from 'ng-zorro-antd';
import {ModalEditPasswordComponent} from '../modal-edit-password/modal-edit-password.component';
import {Router} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UtilServeService} from '../../../share/util/util-serve.service';

@Component({
  selector: 'app-user-manger',
  templateUrl: './user-manger.component.html',
  styleUrls: ['./user-manger.component.scss']
})
export class UserMangerComponent implements OnInit {
  validateForm;
  i = 1;
  isEnablemodal;
  isloading = true;
  dataSet = [];
  allId:any = [];
  constructor(private fb:FormBuilder,
              private msg:NzMessageService,
  private modalService:NzModalService,private router:Router,private url :UrlServeService,private http:ResetHttpService,private util:UtilServeService) {
      this.validateForm = this.fb.group({
      serveArea:[],
      vip:[],
      state:[],
      phone:[],

    })
  }
  //获取组件
  get serveArea(){
    return this.validateForm.controls.serveArea;
  }
  get vip(){
    return this.validateForm.controls.vip;
  }
  get state(){
    return this.validateForm.controls.state;
  }get phone(){
    return this.validateForm.controls.phone;
  }

  ngOnInit() {
    this.getDetailList();
  }
  //页面跳转
  goPager(option){
    this.util.goPage(option);
  }
  //搜索
  searchMore(){
    console.log(this.vip.value,this.state.value,this.phone.value,this.serveArea.value);
    this.getDetailList(this.vip.value,this.state.value,this.phone.value);

  }
  //获取用户列表
  getDetailList(vip ='',status = '',name = ''){
    this.isloading =true;
    this.http.get(this.url.userList,{
      vip_level:vip,
      status:status,
      name:name,
    },data=>{
      if(data.code == '001000'){
        this.dataSet = data.response;
        this.isloading = false;
        this.dataSet.forEach((v,k)=>{
          this.allId.push(v.id);
        });
      }else{
        this.msg.error(data.message);
      }
     // console.log(data);
    },err=>{
      console.log(err);
    })
  }

  /*
  * 表格操作
  * */

  //停用
  isEnable(data){
    // console.log(data);
    this.isEnablemodal = this.modalService.create({
      nzTitle: '',
      nzContent: '停用后，该账户将无法登录使用APP！<br><br>确认停用吗',
      nzClosable: false,
      nzOnOk: () => new Promise((resolve) => {
        this.http.get(this.url.userIsOpen,{
          id:data.id,
          status:'N'
        },res=>{
          if(res.code === '001000'){
            this.getDetailList();
            this.msg.success('修改完成');
            this.isEnablemodal.nzOkLoading = false;
            this.isEnablemodal.destroy();
          }else{
            console.log(data.message);
          }
        },err=>{
          console.log(err);
        });
      }),
      nzOnCancel:() => new Promise((resolve) =>{
        this.isEnablemodal.nzCancelLoading =false;
        this.isEnablemodal.destroy();
      })
    });
  }
  //启用
  isable(data){
    this.http.get(this.url.userIsOpen,{
      id:data.id,
      status:'Y'
    },data=>{
      if(data.code === '001000'){
        this.getDetailList();
        this.msg.success('修改完成')
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })

  }
  //创建模态窗 重置密码
  createModal(data): void {
    this.validateForm.reset();
    var modal = this.modalService.create({
      nzTitle: '重置密码',
      nzContent: ModalEditPasswordComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));

  }
}
