import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService} from 'ng-zorro-antd';
import {UtilServeService} from '../../../share/util/util-serve.service';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.scss']
})
export class AddUserComponent implements OnInit {
  validateForm: FormGroup;
  patt = /\w{6,12}/;
  constructor(private util:UtilServeService,private fb: FormBuilder,private http: ResetHttpService,private router:Router,private url :UrlServeService,private msg:NzMessageService) {
    this.validateForm = this.fb.group({
      invitationNum   : [ '', [ ] ],
      address: [ '', [ Validators.required ] ],
      vip : [ '', [ Validators.required  ] ],
      phoneNum : [ '', [ this.checkPhone ] ]
    });
  }

  ngOnInit() { }
  //提交
  submitForm = ($event, value) => {
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid) return;
    var code_value = this.validateForm.controls['invitationNum'].value;
    if(!code_value){
      code_value = null;
    }
    this.http.get(this.url.addUser, {
      telephone:this.validateForm.controls['phoneNum'].value,
      referee_code:code_value,
      merchant_id:this.validateForm.controls['address'].value,
      vip_level:this.validateForm.controls['vip'].value
    },(data)=>{
      // console.log(data);
      if(data.code == '001000'){
        this.util.initPassword();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  };
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //验证密码
  checkPhone = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  }
}
