import { Component, OnInit } from '@angular/core';
import {NzModalRef} from 'ng-zorro-antd';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UrlServeService} from '../../../share/util/url-serve.service';

@Component({
  selector: 'app-modal-codedetail-user',
  templateUrl: './modal-codedetail-user.component.html',
  styleUrls: ['./modal-codedetail-user.component.scss']
})
export class ModalCodedetailUserComponent implements OnInit {
  dataSet = [];
  customDetail;
  constructor(private nzmref:NzModalRef,private http:ResetHttpService,private url:UrlServeService) { }

  ngOnInit() {
    this.customDetail = this.nzmref.getContentComponent();
    this.getData(this.customDetail.userid,this.customDetail.refreeid);
  }
  getData(uid,rid){
    this.http.get(this.url.userGetRefereeLog,{
      userId:uid,
      refereeId:rid,
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
      }else{
        console.log(data.message);
      }
    },erro=>{
      console.log(erro)
    })
  }

}
