import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AddUserComponent} from './add-user/add-user.component';
import {UserMangerComponent} from './user-manger/user-manger.component';
import {ViewUserComponent} from './view-user/view-user.component';

const routes: Routes = [
  {path: '', component:UserMangerComponent,data:{breadcrumb: '用户管理'}},
  {path: 'add', component:AddUserComponent,data:{breadcrumb: '添加用户'}},
  {path: 'view', component:ViewUserComponent,data:{breadcrumb: '查看用户'}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoutingModule { }
