import {ChangeDetectorRef, Component, OnInit, ViewChild} from '@angular/core';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {ModalCodedetailUserComponent} from '../modal-codedetail-user/modal-codedetail-user.component';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-view-user',
  templateUrl: './view-user.component.html',
  styleUrls: ['./view-user.component.scss']
})
export class ViewUserComponent implements OnInit {
  @ViewChild('tplContent') tplContent;
  showNum = 0;
  dataSet = [];
  params;
  userInfo:any;
  addRess = [];
  cusTomList = [];
  codeList = [];
  allData;
  pageNum;
  allNum;
  constructor(private cdr :ChangeDetectorRef,private modalService:NzModalService,private url :UrlServeService,private http:ResetHttpService,private route:ActivatedRoute,private msg:NzMessageService){
    this.route.queryParams.subscribe(params => {
      this.params = JSON.parse(params.data);
      this.allNum = Math.abs(this.params.allId.length);
    });
  }

  ngOnInit(): void {
    this.params.allId.forEach((v,k)=>{
      if(v == this.params.id){
        this.pageNum = k+1;
      }
    });
    this.getDetailUser(this.params.id);

  }
  //获取用户详情
  getDetailUser(id){
    console.log(id);
    this.http.get(this.url.userDetail,{
      id:id
    },data=>{
      if(data.code == '001000'){
        // console.log(data);
        let res = data.response;
        this.allData = data.response;
        this.userInfo = res.userInfo;
        this.cusTomList = res.refereeInfo;
        this.codeList = res.integral;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  pageChange(e){
    this.getDetailUser(this.params.allId[e-1]);
  }
  ngAfterViewInit(){
    this.cdr.detectChanges();
  }
  //打开积分详情
  detailConsume(data){
    this.modalService.create({
      nzTitle: '',
      nzContent: ModalCodedetailUserComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter: null,
    });
  }
  //打开大图
  createModal(data): void {
    this.modalService.create({
      nzTitle: '',
      nzContent: this.tplContent,
      nzClosable: false,
      nzFooter: null
    });
  }
}
