import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, Validators} from '@angular/forms';
import {NzMessageService, NzModalRef} from 'ng-zorro-antd';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
declare var hex_md5:any;
@Component({
  selector: 'app-modal-edit-password',
  templateUrl: './modal-edit-password.component.html',
  styleUrls: ['./modal-edit-password.component.scss']
})
export class ModalEditPasswordComponent implements OnInit {
  validateForm;
  datas;
  patt = /\w{6,12}/;
  constructor(private fb:FormBuilder,private nzmref:NzModalRef,private http:ResetHttpService,
              private msg:NzMessageService,
              private url:UrlServeService) {
    this.validateForm = this.fb.group({
      oldPassword:['',[this.checkPassword]],
      password:['',[this.checkPassword]]
    })
  }

  ngOnInit() {
    this.datas = this.nzmref.getContentComponent();
  }
  checkPassword = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true};
    }
    if(!this.patt.test(control.value)){
      return {error:true};
    }
  };
  commit(){
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid){
      return;
    }else{
      //请求数据
      this.http.get(this.url.userResetPassword,{
        password:hex_md5(this.validateForm.controls.password),
        managerPassword:hex_md5(this.validateForm.controls.oldPassword),
        id:'1'
      },res=>{
        console.log(res);
        if(res.code == '901015'){
          this.validateForm.controls.oldPassword.setErrors({'fault':'true',dirty:true,errors:true});
        }else if(res.code == '001000'){
          this.msg.success('修改成功');
          //销毁modal
          this.nzmref.destroy({data:'success'});
        }
      },err=>{
        console.log(err);
      })
    }
  }
  cancle(){
    this.nzmref.destroy({data:'取消'});
  }
}
